
.detail {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 32vw;
  left: 68vw;
}
.board {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  padding: 20px;
  overflow-x: auto;
}
.board.has-detail {
    border-right: 1px solid #dbdbdb;
    left: 0;
    right: 32vw;
}
.board .columns {
    max-height: 100%;
    height: 100%;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.board .columns .column {
      min-height: 0px;
      padding: 0;
      margin: 0 10px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
.board .columns .column.new-list {
        width: 330px;
}
.board .columns .column.new-list .card-content {
          padding: .25em;
}
.board .columns .column.new-list .card-content .control {
            padding: .25em;
}
.board .columns .column.new-list .card-content .control .add-list-btn {
              margin-right: 1em;
}



